body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  color: #eee;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

#sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 4vw;
  width: 6vw;
  border-right: solid 2vw #38404C; 
  border-left: solid 2vw #38404C; 
  z-index: 1;
}

#sidebar::after {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 8vw;
  width: 2vw;
  background: #38404C;
  content: ' ';
  z-index: 1;
}

#menu {
  color: #38404C;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 20vw;
  text-align: center;
}

#menu h5 {
  padding: 0;
  margin: 3px 0;
}

#menu p {
  padding: 2px 0;
  margin: 0;
  font-size: 0.8em;
}




